  .top-section{
    padding: 20px;
    font-size: 16px;
}
.home-info{
    position: relative;
    display: flex;
    max-width: 1100px;
    margin: 20px auto 0;
    justify-content: space-between;
    float: right

}
.home-text{
    max-width: 700px;
}
.page-title{
    display: block;
    max-width: 100%;
    margin-top: 30px;
    margin-bottom: 15px;
    font-family: 'Quicksand', sans-serif !important;
    font-size: 45px;
    font-weight: 600;
    color: #334357;
    padding:20px;
    justify-content: center;
    text-align: center;
}
.small-header{
    display: block;
    width: 90%;
    margin: 0 0 40px;
    font-family: 'Quicksand', sans-serif !important;
    font-size: 20px;
    font-weight: normal;
    line-height: 1.5em;
    padding:10px;

}
.social-links {
  text-align: center;
  display: flex;
  justify-content: center;
}
.social-item {
  color: var(--theme-color);
  margin: 5px;
}
.social-flex {
  display: flex;
  align-items: center;
}
a, a:hover, a:active, a:focus, a:visited {
  color: var(--theme-color) !important;
  text-decoration: none !important;
}

.main-see-below {
  text-align: center;
}